<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'工作流管理'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-workflow
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-workflow>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditWorkflow from "./create-or-edit-workflow/create-or-edit-workflow.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "Workflow",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditWorkflow,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该工作流吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const workflowTypes = [
          "生产工作流",
          "质检工作流",
          "采购工作流",
          "销售工作流",
          "研发工作流",
          "服务工作流"
        ];
        
        const departments = [
          "生产部",
          "质量部",
          "采购部",
          "销售部",
          "研发部",
          "客服部"
        ];
        
        const statuses = ["已发布", "草稿", "审核中", "已废弃"];
        
        for (let i = 0; i < 25; i++) {
          const typeIndex = i % 6;
          let workflowCode, name, description, version;
          
          // 根据工作流类型生成不同的数据
          switch(typeIndex) {
            case 0: // 生产工作流
              const productionFlows = [
                { code: "PF001", name: "标准生产工作流", desc: "标准化生产过程管理工作流", ver: "V1.2" },
                { code: "PF002", name: "紧急生产工作流", desc: "紧急订单生产过程管理工作流", ver: "V2.0" },
                { code: "PF003", name: "小批量生产工作流", desc: "定制小批量产品生产工作流", ver: "V1.5" },
                { code: "PF004", name: "大批量生产工作流", desc: "大规模标准产品生产工作流", ver: "V1.1" }
              ];
              const productionIndex = i % 4;
              workflowCode = productionFlows[productionIndex].code;
              name = productionFlows[productionIndex].name;
              description = productionFlows[productionIndex].desc;
              version = productionFlows[productionIndex].ver;
              break;
            
            case 1: // 质检工作流
              const qualityFlows = [
                { code: "QF001", name: "进料检验工作流", desc: "原材料和采购件进料质量检验工作流", ver: "V1.3" },
                { code: "QF002", name: "过程检验工作流", desc: "生产过程中的质量检验工作流", ver: "V2.1" },
                { code: "QF003", name: "成品检验工作流", desc: "最终产品质量检验工作流", ver: "V1.0" }
              ];
              const qualityIndex = i % 3;
              workflowCode = qualityFlows[qualityIndex].code;
              name = qualityFlows[qualityIndex].name;
              description = qualityFlows[qualityIndex].desc;
              version = qualityFlows[qualityIndex].ver;
              break;
            
            case 2: // 采购工作流
              const purchaseFlows = [
                { code: "WF001", name: "常规采购工作流", desc: "常规物料采购审批工作流", ver: "V1.4" },
                { code: "WF002", name: "紧急采购工作流", desc: "紧急物料采购审批工作流", ver: "V1.8" },
                { code: "WF003", name: "外协采购工作流", desc: "外协服务采购审批工作流", ver: "V2.2" }
              ];
              const purchaseIndex = i % 3;
              workflowCode = purchaseFlows[purchaseIndex].code;
              name = purchaseFlows[purchaseIndex].name;
              description = purchaseFlows[purchaseIndex].desc;
              version = purchaseFlows[purchaseIndex].ver;
              break;
            
            case 3: // 销售工作流
              const salesFlows = [
                { code: "SF001", name: "常规销售工作流", desc: "常规产品销售流程管理", ver: "V1.3" },
                { code: "SF002", name: "定制化销售工作流", desc: "定制产品销售流程管理", ver: "V1.7" },
                { code: "SF003", name: "国际销售工作流", desc: "出口产品销售流程管理", ver: "V1.2" }
              ];
              const salesIndex = i % 3;
              workflowCode = salesFlows[salesIndex].code;
              name = salesFlows[salesIndex].name;
              description = salesFlows[salesIndex].desc;
              version = salesFlows[salesIndex].ver;
              break;
            
            case 4: // 研发工作流
              const rdFlows = [
                { code: "RF001", name: "新产品研发工作流", desc: "新产品立项到量产全流程管理", ver: "V1.5" },
                { code: "RF002", name: "产品改良工作流", desc: "现有产品技术改良流程管理", ver: "V1.9" },
                { code: "RF003", name: "技术问题处理工作流", desc: "生产技术问题处理流程管理", ver: "V1.4" }
              ];
              const rdIndex = i % 3;
              workflowCode = rdFlows[rdIndex].code;
              name = rdFlows[rdIndex].name;
              description = rdFlows[rdIndex].desc;
              version = rdFlows[rdIndex].ver;
              break;
            
            case 5: // 服务工作流
              const serviceFlows = [
                { code: "SV001", name: "售后服务工作流", desc: "产品售后服务管理流程", ver: "V1.6" },
                { code: "SV002", name: "客户投诉处理工作流", desc: "客户投诉处理管理流程", ver: "V2.0" },
                { code: "SV003", name: "产品返修工作流", desc: "产品返修处理管理流程", ver: "V1.3" }
              ];
              const serviceIndex = i % 3;
              workflowCode = serviceFlows[serviceIndex].code;
              name = serviceFlows[serviceIndex].name;
              description = serviceFlows[serviceIndex].desc;
              version = serviceFlows[serviceIndex].ver;
              break;
          }
          
          dataList[i] = {
            id: i + 1,
            workflowCode: `${workflowCode}${String(i + 1).padStart(2, "0")}`,
            name: name,
            workflowType: workflowTypes[typeIndex],
            department: departments[i % 6],
            description: description,
            version: version,
            nodeCount: 4 + (i % 7),
            status: statuses[i % 4],
            createdBy: "admin",
            createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
            modifiedBy: "admin",
            modifiedTime: "2024-03-" + (20 + i % 8) + " " + (13 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.workflowCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.workflowType.indexOf(this.filterText) > -1 ||
                   item.department.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1 ||
                   item.version.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style>